import { legacy_createStore as createStore,combineReducers, applyMiddleware } from 'redux'
import thunk from 'redux-thunk'
import { composeWithDevTools } from 'redux-devtools-extension'
import { productListReducers,productDetailsReducers,productDeleteReducers,productCreateReducers, productUpdateReducers ,productReducers} from './reducers/productReducer'
import { cartReducer } from './reducers/cartReducer'
import { userLoginReducers, userRegisterReducers, userDetailsReducers,userUpdateProfileReducers,userListReducers, userDeleteReducers, userUpdateReducers } from './reducers/userReducer'
import { orderCreateReducer,orderDetailsReducer,orderPayReducer,orderListMyReducer, orderListReducer,orderDeliverReducer } from './reducers/orderReducer'
const reducer= combineReducers({
    productList:productListReducers,
    productDetails:productDetailsReducers,
    productDelete:productDeleteReducers,
    productCreate:productCreateReducers,
    productUpdate:productUpdateReducers,
    product:productReducers,
    cart:cartReducer,
    userLogin:userLoginReducers,
    userRegister:userRegisterReducers,
    userDetails:userDetailsReducers,
    userUpdateProfile:userUpdateProfileReducers,
    orderCreate:orderCreateReducer,
    orderDetails:orderDetailsReducer,
    orderPay:orderPayReducer,
    orderListMy:orderListMyReducer,
    orderList:orderListReducer,
    orderDeliver:orderDeliverReducer,
    userList:userListReducers,
    userDelete:userDeleteReducers,
    userUpdate:userUpdateReducers,
})
const cartItemsFromStorage=localStorage.getItem('cartItems')?
            JSON.parse(localStorage.getItem('cartItems')):[]

const userInfoFromStorage=localStorage.getItem('userInfo')?
            JSON.parse(localStorage.getItem('userInfo')):null

const shippingAddressFromStorage=localStorage.getItem('shippingAddress')?
            JSON.parse(localStorage.getItem('shippingAddress')):{}           
            
const initialState = {
    cart:{
        cartItems:cartItemsFromStorage,
        shippingAddress:shippingAddressFromStorage,
    },
    userLogin:{userInfo:userInfoFromStorage}
}
const middleware =[thunk]
const store = createStore(reducer,initialState,composeWithDevTools(applyMiddleware(...middleware)))

export default store